<template>
  <div class="index-page">
    <div class="banner-wrap">
      <img src="https://image.haoxuezhuli.com/wap-index/banner.png" class="banner">
      <button class="banner-btn bg-color" @click.stop.prevent="applyUse"></button>
    </div>
    <div class="content-wrap">
      <div class="swiper-wrap">
        <h3>
          <br>
        </h3>
        <div class="swiper-inner">
          <swiper :options="swiperOption" ref="mySwiper1" @slideChange="callback(1)">
            <swiper-slide v-for="(item,index) in swiperArr1" :key="index">
              <div class="swiper">
                <img :src="item.img" class="img">
                <p class="text">{{item.text}}</p>
              </div>
            </swiper-slide>
          </swiper>
          <div class="pagination-wrap">
            <span
              class="pagination"
              :class="{'bg-color' : pagination1 == index}"
              v-for="(item,index) in swiperArr1"
              :key="index"
            ></span>
          </div>
        </div>
      </div>

      <div class="swiper-wrap" style="background-color: #fdf8ff">
        <h3>
          <br>
        </h3>
        <div class="swiper-inner">
          <swiper :options="swiperOption" ref="mySwiper2" @slideChange="callback(2)">
            <swiper-slide v-for="(item,index) in swiperArr2" :key="index">
              <div class="swiper">
                <img :src="item.img" class="img">
                <p class="text">{{item.text}}</p>
              </div>
            </swiper-slide>
          </swiper>
          <div class="pagination-wrap">
            <span
              class="pagination"
              :class="{'bg-color' : pagination2 == index}"
              v-for="(item,index) in swiperArr2"
              :key="index"
            ></span>
          </div>
        </div>
      </div>

      <div class="swiper-wrap">
        <h3>
          <br>
        </h3>
        <div class="swiper-inner">
          <swiper :options="swiperOption" ref="mySwiper3" @slideChange="callback(3)">
            <swiper-slide v-for="(item,index) in swiperArr3" :key="index">
              <div class="swiper">
                <img :src="item.img" class="img">
                <p class="text">{{item.text}}</p>
              </div>
            </swiper-slide>
          </swiper>
          <div class="pagination-wrap">
            <span
              class="pagination"
              :class="{'bg-color' : pagination3 == index}"
              v-for="(item,index) in swiperArr3"
              :key="index"
            ></span>
          </div>
        </div>
      </div>
    </div>

    <button class="footer-btn bg-btn" @click.stop.prevent="applyUse"></button>
  </div>
</template>

<script >
// require styles
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  data() {
    return {
      swiperOption: {
        height: 600,
        autoplay: {
          delay: 3000,
          stopOnLastSlide: false,
          disableOnInteraction: false
        }
      },

      pagination1: 0,
      swiperArr1: [
        {
          img: "https://image.haoxuezhuli.com/wap-index/b1-1.png",
          text: ""
        },
        {
          img: "https://image.haoxuezhuli.com/wap-index/b1-2.png",
          text: ""
        },
        {
          img: "https://image.haoxuezhuli.com/wap-index/b1-3.png",
          text:
            ""
        }
      ],

      pagination2: 0,
      swiperArr2: [
        {
          img: "https://image.haoxuezhuli.com/wap-index/b2-1.png",
          text:
            ""
        },
        {
          img: "https://image.haoxuezhuli.com/wap-index/b2-2.png",
          text:
            "/"
        }
      ],

      pagination3: 0,
      swiperArr3: [
        {
          img: "https://image.haoxuezhuli.com/wap-index/b3-1.png",
          text: ""
        },
        {
          img: "https://image.haoxuezhuli.com/wap-index/b3-2.png",
          text: ""
        }
      ]
    };
  },
  methods: {
    // 
    callback(type) {
      this["pagination" + type] = this["swiper" + type].activeIndex;
    },

    //
    applyUse() {
      this.$router.push({ name: "apply" });
    }
  },
  computed: {
    swiper1() {
      return this.$refs.mySwiper1.swiper;
    },
    swiper2() {
      return this.$refs.mySwiper2.swiper;
    },
    swiper3() {
      return this.$refs.mySwiper3.swiper;
    }
  },
  components: {
    swiper,
    swiperSlide
  }
};
</script>

<style scoped lang="stylus">
.index-page
  min-height: 100%;
  background-color: #fff;
  padding-bottom: 100px;
  .banner-wrap
    position: relative;
    .banner
      width: 100%;
      height: auto;
    .banner-btn
      position: absolute;
      bottom: 130px;
      left: 50%;
      transform: translateX(-50%);
      width: 400px;
      height: 80px;
      line-height: 80px;
      text-align: center;
      font-size: 32px;
      color: #fff;
      border-radius: 4px;
  .content-wrap
    .swiper-wrap
      h3
        font-size: 54px;
        color: #333;
        font-weight: 600;
        padding: 100px 0;
        text-align: center;
        line-height: 1.3;
      .swiper-inner
        .swiper
          padding: 0 20px;
          .img
            width: 100%;
            height: 414px;
          .text
            padding: 30px 0;
            font-size: 32px;
            color: #868ce1;
            text-align: center;
            line-height: 1.3;
      .pagination-wrap
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 90px 0 120px 0;
        .pagination
          width: 100px;
          height: 6px;
          margin-right: 20px;
          background-color: #eee;
          &:last-child
            margin-right: 0;
.footer-btn
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100px;
  line-height: 100px;
  text-align: center;
  font-size: 32px;
  color: #fff;
  z-index: 9;
</style>


